import styles from "../marketing.module.scss";
import arrow_white from "../../../../resources/img/Events/Marketing/arrow_white.png";
import sec01_btn from '../../../../resources/img/Events/Marketing/sec01_btn.png';
import sec01_light00 from '../../../../resources/img/Events/Marketing/sec01_light_m.png';
import sec01_light01 from '../../../../resources/img/Events/Marketing/sec01_light01_m.png';
import sec01_light02 from '../../../../resources/img/Events/Marketing/sec01_light02_m.png';
import sec01_light03 from '../../../../resources/img/Events/Marketing/sec01_light03_m.png';
import sec01_light04 from '../../../../resources/img/Events/Marketing/sec01_light04_m.png';

export const Section01 = (props) => {
    return (
        <>
            <div className={`${styles.sec} ${styles.sec01} ${styles.rel} ${props.play && styles['sec01--off']}`}>
                <button className={`${styles.sec01__btn} ${props.play && styles.sec01__click01} ${styles.ab}`}
                        onClick={props.onPlay}>
                    <img className={`${styles.sec01__btn__img} ${styles.ab}`} src={sec01_btn} alt="빗썸 로고 들어간 사각형 버튼" />
                    <div className={`${styles.sec01__btn__light}`}>
                        <img className={`${styles.sec01__btn__light__light00} ${props.play && styles['sec01__btn__light__light00--off']} ${styles.ab}`} src={sec01_light00} alt="빛" />
                        <img className={`${styles.sec01__btn__light__light01} ${props.play && styles['sec01__btn__light__light01--on']} ${styles.ab}`} src={sec01_light01} alt="빛 1" />
                        <img className={`${styles.sec01__btn__light__light02} ${props.play && styles['sec01__btn__light__light02--on']} ${styles.ab}`} src={sec01_light02} alt="빛 2" />
                        <img className={`${styles.sec01__btn__light__light03} ${props.play && styles['sec01__btn__light__light03--on']} ${styles.ab}`} src={sec01_light03} alt="빛 3" />
                        <img className={`${styles.sec01__btn__light__light04} ${props.play && styles['sec01__btn__light__light04--on']} ${styles.ab}`} src={sec01_light04} alt="빛 4" />
                    </div>
                </button>
                <div className={`${styles.sec01__arrowFont} ${styles.ab}`}>
                    <img className={styles.sec01__arrowFont__arrow} src={arrow_white} alt="터치 화살표" />
                    <p className={`${styles.sec01__arrowFont__font} ${styles.averta} ${styles.white}`}>TOUCH</p>
                </div>
                <p className={`${styles.sec01__font} ${styles.notoBold} ${props.play && styles.sec01__click03} ${styles.ab} ${styles.marginDel}`}>확 바꿨습니다</p>
            </div>
            <div className={`${styles['sec01__left-box']} ${props.play && styles['sec01__left-box--move']}`} />
            <div className={`${styles['sec01__right-box']} ${props.play && styles['sec01__right-box--move']}`} />
            <div className={`${styles['sec01__top-box']} ${props.play && styles['sec01__top-box--move']}`} />
            <div className={`${styles['sec01__bottom-box']} ${props.play && styles['sec01__bottom-box--move']}`} />
        </>
    )
}